          @import "src/assets/variables";
          @import "src/assets/fonts";
        
.card {
  min-height: 300px;
  min-width: 400px;
  max-width: 400px;
  background-color: $secondary-color;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.list{
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin: 0px 0 5px;
}

.tag_list {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.card_image {
  width: 90%;
  height: 40%;
  border-radius: 20px;
}

.title{
  text-align: center;
}

.info{
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.ages{
  margin: 0;
}