.section{
  background-color: $secondary-color;
  width: 60%;
  border-radius: 30px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  & > form {
    display: flex;
    align-items: center;
    flex-direction: column;
    & > button {
      display: flex;
      justify-content: center;
      width: 90%;
    }
  }
}

.main{
  min-height: calc(100vh - 90px);
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.main h1 {
  text-align: center;
  font-size: 24pt;
}

.link {
  font-weight: 700;
  color: $primary-color;
  margin-top: 30px;
}