.main{
  min-height: calc(100vh - 90px);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section{
  background: $secondary-color;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  padding: 20px;
  box-sizing: border-box;
  margin: 40px;
  height: 100%;
  width: 100%;

  & > button {
    width: fit-content;
  }
}
