.aside {
  background: $secondary-color;
  margin: 0 20px 50px;

  padding: 20px;
  justify-content: flex-start;
  border-radius: 20px;
  width: 100%;
  box-sizing: border-box;
  max-height: 610px;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  & > div {
    width: 93%;
    & > div {
      width: 100%;
      & > div {
        & > input::placeholder{
          color: #9AA0B4;
        }
      }
      margin-bottom: 20px;
    }
    font-family: "Open Sans", serif;
    color: #9AA0B4;
  }
}

.ages {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 50px;
}

.checks{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.buttons{
  display: flex;
  justify-content: space-between;
}