          @import "src/assets/variables";
          @import "src/assets/fonts";
        
.input {
  background-color: $input-color;
  border: none;
  width: 90%;
  height: 50px;
  padding-left: 10px;
  font-size: 16pt;
  text-indent: 15px;
  border-radius: 5px;
  margin: 0 auto 20px;
  color: #9AA0B4;

  &::placeholder{
    color: #9AA0B4;
  }

  &:focus {
    box-shadow: 2px 2px 2px 2px $primary-color;
    border: none;
  }
  &:focus-visible {
    border: none;
    outline: none;
  }
}