          @import "src/assets/fonts";
        
.input {
  position: relative;
  width: fit-content;
  display: inline-block;
  margin-left: 14.3%;
  margin-bottom: 20px;
}
.btn {
  height: 50px;
  box-sizing: border-box;
  font-family: "Open Sans";
  width: fit-content;
  font-weight: 400;
  display: flex;
  align-items: center;
  border: 4px solid $primary-color;
  position: relative;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 16pt;
  vertical-align: middle;
  color: rgb(255 255 255);
  text-align: center;
  border-radius: 4px;
  background-color: $primary-color;
  height: 40px;
  padding: 10px 20px;
  margin: 0;
  transition: background-color 0.2s;
}
.text {
  padding: 0 10px;
  line-height: 40px;
  display: inline-block;
}
.input input[type=file] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

/* Focus */
.input input[type=file]:focus + .btn {
  box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
}

/* Hover/active */
.input:hover .btn {
  color: $primary-color;
  background-color: transparent;

}
.input:active .btn {
  background-color: transparent;
  color: $primary-color;
}

/* Disabled */
.input input[type=file]:disabled + .btn {
  background-color: #eee;
}