.page {
  background-color: $background-color;
  min-height: calc(100vh - 90px);
  box-sizing: border-box;
  padding-top: 50px;
  display: flex;
  flex-direction: row;

}

.event_section{
  width: 75%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: flex-start;
  gap: 40px;
  row-gap: 20px;
}

.aside_section{
  display: flex;
  justify-content: center;
  width: 25%;
}