
                @import "src/assets/combo";
          @import "src/assets/variables";
          @import "src/assets/fonts";
        
.main{
  min-height: calc(100vh - 90px);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section{
  background: $secondary-color;
  border-radius: 20px;
  padding: 20px;
  box-sizing: border-box;
  margin: 40px;
  height: 100%;
  width: 100%;
  gap: 20px;
}

.top{
  max-height: 250px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-direction: row;
  margin-bottom: 40px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.file {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
}

.file > a{
  display: flex;
  color: $primary-color;
  gap: 10px;

  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  & > svg{
    width: 32px;
    height: 32px;
  }
}

.logo_wrap{
  width: 20%;
}

.image_wrap{
  max-height: 250px;
  width: 75%;
  margin: 0 auto;
}

.image_wrap > img{
  height: 100%;
}
.text{
  display: block;
  margin-top: 20px;
}