          @import "src/assets/variables";
          @import "src/assets/fonts";
        


.section{
  background-color: $secondary-color;
  width: 60%;
  border-radius: 30px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  & > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    & > button{
      display: flex;
      justify-content: center;
      width: 90%;
    }
    & > div {
      width: 92%;
      & > div {
        width: 100%;
      }
      margin-bottom: 20px;
      box-sizing: border-box;
      font-family: "Open Sans", serif;
      color: $secondary-color;
    }
  }
}

.main{
  min-height: calc(100vh - 90px);
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.main h1 {
  text-align: center;
  font-size: 24pt;
}

